/**
* Reset some basic elements
*/
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0;
}



/**
* Basic styling
*/
body {
    font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
    color: $text-color;
    background: $background;
    -webkit-text-size-adjust: 100%;
    -webkit-font-feature-settings: "kern" 1;
    -moz-font-feature-settings: "kern" 1;
    -o-font-feature-settings: "kern" 1;
    font-feature-settings: "kern" 1;
    font-kerning: normal;
}



/**
* Set `margin-bottom` to maintain vertical rhythm
*/
h1, h2, h3, h4, h5, h6 {
    margin-bottom: 0px;
}

p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
    margin-bottom: 20px;
}



/**
* Images
*/
img {
    max-width: 100%;
    vertical-align: middle;
}



/**
* Figures
*/
figure > img {
    display: block;
}

figcaption {
    font-size: $small-font-size;
}



/**
* Lists
*/
ul, ol {
    margin-left: $spacing-unit;
}

li {
    > ul,
    > ol {
        margin-bottom: 0;
    }
}



/**
* Headings
*/
h1, h2, h3, h4, h5, h6 {
    font-family: $header-font-family;
    font-weight: $base-font-weight;
}



/**
* Links
*/
a {
    transition: all .1s ease-in-out;
    -moz-transition: all .1s ease-in-out;
    -webkit-transition: all .1s ease-in-out;
    
    color: $bright;
    text-decoration: none;
    
    &:visited {
        color: $bright
    }
    
    &:hover {
        color: $royal;
        text-decoration: none;
    }
}



/**
* Blockquotes
*/
blockquote {
    color: $grey-color;
    border-left: 4px solid $grey-color-light;
    padding-left: $spacing-unit / 2;
    font-size: 18px;
    letter-spacing: -1px;
    font-style: italic;
    
    > :last-child {
        margin-bottom: 0;
    }
}



/**
* Code formatting
*/
pre,
code {
    font-size: 15px;
    border: 1px solid $grey-color-light;
    border-radius: 3px;
    background: #eef;
}

code {
    padding: 1px 5px;
}

pre {
    padding: 8px 12px;
    overflow-x: auto;
    
    > code {
        border: 0;
        padding-right: 0;
        padding-left: 0;
    }
}



/**
* Wrapper
*/
.wrapper {
    max-width: $content-width;
    margin: 0px auto;
    
    @media screen and (max-width: $content-width + 10px) {
        max-width: 100%;
        margin: 0px 10px;
    }
}

/**
* Clearfix
*/
%clearfix {
    
    &:after {
        content: "";
        display: table;
        clear: both;
    }
}



/**
* Icons
*/
.icon {
    
    > svg {
        display: inline-block;
        width: 16px;
        height: 16px;
        vertical-align: middle;
        
        path {
            fill: $grey-color;
        }
    }
}
